module.exports = "<div class=\"col-md-12 col-sm-12 col-xs-12 np payment-history\">\n    <div class=\"col-md-12 col-sm-12 col-xs-12 np payment-history-details-header\">\n        <div class=\"col-md-8 col-sm-8 col-xs-6 np\">\n            <h6 class=\"\">Invoices</h6>\n        </div>\n        <div class=\"col-md-4 col-sm-4 col-xs-6 np membership-top-wrapper-sec1-right\">\n            <ul class=\"\">\n                <!-- <li>\n                    <a href=\"javascript:void(0);\" class=\"\">\n                        <span class=\"mat-icon\">\n                            <i class=\"material-icons\">help</i>\n                        </span>\n                        &nbsp;Billing FAQs\n                    </a>\n                </li> -->\n              <li>\n                <a href=\"javascript:void(0);\" class=\"intercom_trigger\" (click)=\"contactUs()\">\n                  <span class=\"mat-icon\">\n                    <i class=\"material-icons\">chat_bubble</i>\n                  </span> &nbsp;Contact Us\n\n                </a>\n              </li>\n            </ul>\n        </div>\n    </div>\n\n    <div class=\"col-md-12 col-sm-12 col-xs-12 text-center\" *ngIf = \"loadingInvDet\"><i class=\"material-icons loader-plan\">autorenew</i></div>\n    <div class=\"payment-history-dialog\"  *ngIf = \"!loadingInvDet\">\n        <!-- Modal content-->\n        <div class=\"payment-history-content\">\n            <!-- <div class=\"payment-history-header \">\n                <button type=\"button\" class=\"close btn-close\" data-dismiss=\"modal\" aria-label=\"Close\">\n                    <i class=\"material-icons\">close</i>\n                </button>\n                <h5 class=\"payment-history-title\">Payment History</h5>\n            </div> -->\n            <div class=\"payment-history-body col-md-12 np\">\n                <div class=\"sahil-material\">\n                    <div class=\"table-responsive\">\n                        <table class=\"table\">\n                            <thead>\n                                <tr>\n                                    <th>Invoice No.</th>\n                                    <th>Date</th>\n                                    <th>Amount paid</th>\n                                    <th>Amount due</th>\n                                    <th>Status</th>\n                                    <th>&nbsp;</th>\n                                </tr>\n                            </thead>\n                            <tbody *ngIf=\"isInvoiceExist\">\n                                <tr *ngFor=\"let invoice of allInvoices;let i = index\">\n                                    <td>{{i+1}}</td>\n                                    <td>{{invoice.date}}</td>\n                                    <td>$ {{invoice.amount_paid}}</td>\n                                    <td>$ {{invoice.amount_due}}</td>\n                                    <td class=\"text-opacity\">\n                                        <span [class.invoice-paid]=\"invoice.status=='paid'\"\n                                                [class.invoice-not-paid]=\"invoice.status=='not_paid'\"\n                                                [class.invoice-voided]=\"invoice.status=='voided'\"\n                                                [class.invoice-due]=\"invoice.status=='payment_due'\"\n                                                [class.invoice-pending]=\"invoice.status=='pending'\">\n                                            {{invoice.status}}\n                                        </span>\n                                        <!-- <span *ngIf=\"invoice.status=='paid'\"> via {{invoice.cardDetail}} </span> -->\n                                    </td>\n                                    <td>\n                                        <a href=\"javascript:void(0);\" class=\"invoiceId pdf-download\" (click)=\"getInvoicePdf(invoice.id)\">\n                                            Download PDF\n                                        </a>\n                                    </td>\n                                </tr>\n                            </tbody>\n                            <tbody *ngIf=\"!isInvoiceExist\">\n                                <tr>\n                                    <td></td>\n                                    <td></td>\n                                    <td colspan=\"5\" class=\"no-invoice\">\n                                        No Invoice has been raised\n                                    </td>\n                                    <td></td>\n                                    <td></td>\n                                </tr>\n                            </tbody>\n                        </table>\n                    </div>\n                </div>\n            </div>\n            <div class=\"payment-history-footer col-md-12 np\">\n                <div class=\"col-md-5 np\"></div>\n                <div class=\"col-md-7 np\">\n                    <!--<button type=\"\" class=\"btn btn-red-outline btn-hover\">Approve Request</button>-->\n                    <ul class=\"pagination hide\">\n                        <li><a href=\"javascript:void(0);\">1</a></li>\n                        <li><a href=\"javascript:void(0);\">2</a></li>\n                        <li><a href=\"javascript:void(0);\">3</a></li>\n                        <li><a href=\"javascript:void(0);\">4</a></li>\n                        <li><a href=\"javascript:void(0);\">5</a></li>\n                        <li><a href=\"javascript:void(0);\"><i class=\"material-icons\">play_arrow</i></a></li>\n                        <li><a href=\"javascript:void(0);\"><i class=\"material-icons\">skip_next</i></a></li>\n                    </ul>\n                </div>\n            </div>\n        </div>\n    </div>\n</div>\n"

