ils -->\n\n    <!-- Start: payment-method -->\n    <div class=\"col-md-12 col-sm-12 col-xs-12 np payment-method\" *ngIf=\"!loadingPayDet\">\n        <div class=\"col-md-12 col-sm-12 col-xs-12 np\">\n            <div class=\"col-md-12 col-sm-12 col-xs-12 np payment-details-header\">\n                <div class=\"col-md-8 col-sm-8 col-xs-12 np\" *ngIf=\"userRole == 'ADMIN'\">\n                    <h6 class=\"\">Payment Method</h6>\n                </div>\n            </div>\n\n            <!-- Start: membership-error-msg -->\n            <div class=\"col-md-12 col-sm-12 col-xs-12 np\" *ngIf=\"CardDetail.status == 'expiring'\">\n                <div class=\"membership-error-msg\">\n                    <span class=\"mat-icon\">\n                        <i class=\"material-icons\">report_problem</i>\n                    </span>\n                    <span>Your card will expire by end of this month. For uninterrupted services, please update your card details before your next billing.</span>\n                </div>\n            </div>\n            <!-- End: membership-error-msg -->\n            <div class=\"col-md-12 col-sm-12 col-xs-12 np payment-method-sec\" *ngIf=\"BillingDetails.card_status != 'no_card'\">\n                <div class=\"table-responsive\">\n                    <table class=\"table\">\n                        <thead>\n                            <tr>\n                                <th>Card Type</th>\n                                <th>Account Linked to</th>\n                                <th>Expiry</th>\n                            </tr>\n                        </thead>\n                        <tbody>\n                            <tr>\n                                <td>\n                                    <span class=\"span-input col-md-10 np ellipsis\">{{CardDetail.card_type}}</span>\n                                </td>\n                                <td>\n                                    <span class=\"span-input col-md-10 col-sm-10 col-